<template>
  <h1>{{ count }}</h1>
</template>

<script>
import { defineComponent } from 'vue'
import settings from './settings'

export default defineComponent({
  name: 'BCounter',
  showName: '计数',
  settings,
  placeWidth: 1,
  placeHeight: 1,
})
</script>

<script setup>
import { computed, ref, watch } from 'vue'

const props = defineProps({
  begin: {
    type: Number,
    default: 0,
  },
})
const count = ref(props.begin)
watch(
  () => props.begin,
  () => {
    count.value = isBegin.value ? props.begin : count.value
  },
)
const interval = ref(-1)

const isCounting = computed(() => interval.value !== -1)

const clearInterval = () => {
  window.clearInterval(interval.value)
  interval.value = -1
}

const isBegin = ref(true)
const start = () => {
  interval.value = isCounting.value
    ? interval
    : window.setInterval(() => count.value++, 1000)
  isBegin.value = false
}

const stop = clearInterval

const clear = () => {
  clearInterval()
  count.value = props.begin
  isBegin.value = true
}

const toggle = () => {
  isCounting.value ? stop() : start()
}

defineExpose({
  count,
  isCounting,
  start,
  stop,
  clear,
  toggle,
})
</script>
